import { RelationField as RelationFieldType } from "api/collectionTypes";
import RelationField from "components/RelationField";
import { FieldComponentProps } from "./String";

const Relation: React.FC<FieldComponentProps<RelationFieldType>> = ({
  value,
  onValue,
  field,
  ground,
}) => {
  return (
    <RelationField
      ground={ground}
      onValue={(docIds) => onValue(docIds)}
      fieldType={field}
      value={value}
    ></RelationField>
  );
};

export default Relation;
