import classNames from "classnames";
import { TextareaHTMLAttributes } from "react";
import { GroundType } from "utils/types";

export interface TextareaProps extends TextareaHTMLAttributes<HTMLTextAreaElement> {
  className?: string;
  ground: GroundType;
}

const Textarea: React.FC<TextareaProps> = ({ className = "", ground, ...rest }) => {
  return (
    <textarea
      {...rest}
      className={classNames(
        "border-2 rounded px-3 py-2 w-full min-h-208px outline-none",
        className,
        `input${ground}Background input${ground}HoverBackground input${ground}HoverTextColor input${ground}FocusedBorderColor input${ground}FocusedBackground input${ground}FocusedTextColor`
      )}
    ></textarea>
  );
};

export default Textarea;
