import { AnyField, StringField } from "api/collectionTypes";
import Input from "components/Input";
import ColorPicker from "components/ColorPicker";

export interface FieldComponentProps<T> {
  value: any;
  onValue: (val: any) => void;
  name: string;
  field: T;
  level: number;
}

const Color: React.FC<FieldComponentProps<StringField>> = ({ field, value, onValue, name }) => {
  return (
    <ColorPicker value={value} onValue={(clr) => onValue(clr)}>
      <div style={{ background: value }} className="h-28px w-32 rounded border-2"></div>
    </ColorPicker>
  );
};

export default Color;
