import { AnyField } from "api/collectionTypes";

const initialValues = {
  password: () => "",
  string: () => "",
  "rich-text": () => "",
  json: () => ({}),
  map: () => ({}),
  array: () => [],
  media: (x: AnyField) => (x.type === "media" && x.mediaSingle ? "" : []),
  date: () => null,
  enum: () => null,
  number: () => null,
  boolean: () => false,
  relation: (x: AnyField) => (x.type === "relation" && x.relationOneToOne ? "" : []),
  collection: () => [],
  document: (x: AnyField) =>
    x.type === "document" &&
    x.documentFields.reduce((a, b) => ({ ...a, [b.id]: initialValues[b.type] }), {}),
  any: () => "",
  "hashed-password": () => "",
  color: () => "",
};

export default initialValues;
