import React from "react";
import { getDocument } from "api/collections";
import { useSelector } from "react-redux";
import { RootState } from "store";
import { useHistory } from "react-router-dom";
import DocumentForm from "components/DocumentForm";
import CollectionsManager from "facades/CollectionsManager";
import { useQuery } from "react-query";

export interface DocumentProps {
  collectionId: string;
  docId: string;
}

const Document: React.FC<DocumentProps> = ({ collectionId, docId }) => {
  const collectionType = useSelector((state: RootState) =>
    state.collectionTypes.find((x) => x.id === collectionId)
  );

  const { data: doc } = useQuery([collectionId, "getDocument", docId, collectionType], async () => {
    if (!collectionType) return null;
    if (!docId && !collectionType.single) return null;
    const res = await getDocument(collectionId, docId);
    return res;
  });

  const history = useHistory();

  return !collectionType ? null : (
    <DocumentForm
      level={0}
      ground="Ground"
      docId={docId}
      editableDocument={doc ? doc : undefined}
      fields={collectionType.fields}
      singleName={collectionType.single ? collectionType.name : undefined}
      onSubmit={async (vals) => {
        if (vals) {
          if (doc != null) {
            await CollectionsManager.updateDocument(collectionType.id, doc, vals);
          } else {
            await CollectionsManager.addDocument(collectionType.id, vals);
          }
        }

        if (!collectionType.single) {
          history.goBack();
        }
      }}
    ></DocumentForm>
  );
};

export default Document;
