import { SimpleField } from "api/collectionTypes";
import Input from "components/Input";
import { FieldComponentProps } from "./String";

const Date: React.FC<FieldComponentProps<SimpleField>> = ({ value, onValue, ground, name }) => {
  return (
    <Input
      ground={ground}
      className="h-28px"
      type="date"
      name={name}
      onChange={(e) => onValue(e.target.value)}
      value={value}
    ></Input>
  );
};

export default Date;
